/* menu toogle */
.toggle {
  position: relative;
  width: 40px;
  height: 40px;
  background: url("../../../public/images/menu.png");
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: center;
  cursor: pointer;
  transition: 0.5s ease;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  border-radius: 50%;
}

.toggle:hover, .toggle:active, .toggle.active:hover {
transform: scale(1.1);
}

.toggle.active {
  background: url("../../../public/images/close.png");
  background-repeat: no-repeat;
  background-size: 25px;
  background-position: center;
  cursor: pointer;
}

// side menu
.side-menu {
  position: fixed;
  left: -100vw;
  top: 200px;
  background: $grd-main;
  width: 100vw;
  min-height: calc(100vh - 200px);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.7s ease;
  z-index: 30;
  ul {
    position: relative;
    list-style: none;
    li {
      a {
        text-decoration: none;
        p {
          font-size: 1.5rem;
          color: $clr-light;
        }
      }
    }
  }
}

.side-menu.active {
  transition: 0.7s ease;
  left: 0px;
  z-index: 20;
}


@media (min-width: 1000px) {
  .side-menu {
    display: none;
    
  }
  .menuToggler {
    display: none;
  }
  .menuToggler.user {
    display: none;
  }
}

@media (max-width: 1000px) {
  .side-menu {
    display:flex;
  }
  .menuToggler {
    z-index: 100;
    filter: invert(1);
    display: flex;
    align-items: center;
  }
  .menuToggler.user {
    display: none;
  }
}

@media (max-width: 600px) {
  .menuToggler.user {
    width: fit-content;
    z-index: 100;
    filter: invert(0.7);
    display: flex;
    align-items: center;
  }
}