.register{

.m-5-auto {
	margin: 3rem auto
}
	form {
		display: inline-block;
		background: #f3f3f3;
		border: 1px solid #ddd;
		border-radius: 2px;
		padding: 2rem;
		margin: 2rem 0 1rem 0
	}

	form label {
		float: left;
		font-size: .9rem;
		margin: 0;
		padding: 0
	}

	.right-label {
		float: right;
		cursor: pointer
	}

	input {
		width: 15rem;
		padding: .3rem;
		border-radius: 5px;
		outline: none;
		border: none
	}
		#checkbox {
			width: 1rem
		}
		
		form span {
			font-size: .8rem
		}
		
		#reset_pass_lbl {
			float: left
		}

		h5{
			
			font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
			color: #212529;
			text-align: center!important;
			box-sizing: border-box;
			margin-top: 0;
			margin-bottom: .5rem;
			font-weight: 500;
			line-height: 1.2;
			font-size: 1.25rem;
		}
		

	.primary-button {
		margin-top: 5rem;
		margin-right: 1rem;
		padding: .6rem;
		width: 10rem;
		background: #222;
		border: none;
		color: #fff;
		font-size: 1.2rem;
		transition: all .5s;
		cursor: pointer;
		text-transform: capitalize
	}

	h2 {
		-webkit-text-size-adjust: 100%;
		-webkit-tap-highlight-color: transparent;

		font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
		color: #212529;
		text-align: center!important;
		box-sizing: border-box;
		margin-top: 0;
		margin-bottom: .5rem;
		line-height: 1.2;
		font-size: 2rem;
		font-weight: 300;
	}
	a{
		-webkit-text-size-adjust: 100%;
		-webkit-tap-highlight-color: transparent;
		
		font-weight: 400;
		line-height: 1.5;
		text-align: center!important;
		font-size: .8rem;
		box-sizing: border-box;
		color: #007bff;
		text-decoration: none;
		background-color: transparent;
	}

	p{
		font-size: 1.2rem;
		font-weight: 300;
		line-height: 1.5;
		margin-bottom: 1rem;
		margin-top: 0;
	}

	#sub_btn {
		display: block;
		width: 100%;
		padding: .3rem;
		border: none;
		background: rgb(5, 13, 116);
		color: #fff;
		border-radius: 3px;
	}

	#sub_btn:hover {
		background: #333;
		transition: all .5s
	}

	footer p {
		-webkit-text-size-adjust: 100%;
		-webkit-tap-highlight-color: transparent;
		font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
		font-size: 1rem;
		font-weight: 400;
		line-height: 1.5;
		color: #212529;
		text-align: center!important;
		box-sizing: border-box;
		display: block;
	}


}


@media (max-width: 600px) {
	.register{
    .m-5-auto {
        margin-top: -10rem
    }
	}
}